* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#Header{
  background-color: rgb(250, 250, 125);
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.classDay{
  background-color: rgb(222 143 143);
    display: inline-flex;
    width: 94%;
    margin-left: 3%;
    height: 51px;
    justify-content: space-between;
    align-items: center;
    border-radius: 17px;
}
.classDay:hover{
  background-color: rgba(222, 143, 143, 0.726);
  transition: background-color 0.3s ease-in-out;
}
.classDay.selected{
  background-color: red;
}
.dayCirclce{
  border-radius: 50%;
  width: 44px;
  height: 43px;
  margin: 3%;
  margin-right: 5%;
  text-align: center;
  box-shadow: 0px 0px 5px rgb(0 0 0 / 70%);
}
.classText{
  font-size: 22px;
    margin-left: 14px;
}
.DayNumber{
  margin-top: 5px;
}
.afterDivSpace{
  height: 14px;
  width: 100%;
}